main {
  margin: 0 auto;
  max-width: 90%;
  padding: 20px 20px;
}
:root {
  --primary: #8c54ff;
  --secondary: #61fa4f;
  --tertiary: #b12704;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: 0.1em;
  background: var(--light);
  color: var(--darker);
  font-family: "arial";
  background-image: url("./assets/cover/blue-circular-background.jpg");
  background-size: cover;
  background-attachment: fixed;
  background-position: 80%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Courier New", Courier, monospace;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
  /* font-style: oblique; */
  color: var(--darker);
  text-shadow: 0 0 8px var(--secondary);
  
}

#blurb {
  /* background-color: rgba(49.92%, 50.2%, 56.7%, 0.4); */
  background-color: rgba(95%, 95%, 95%, .4);
  padding: 10px;
  border-radius: 10px;
}
h2 {
  font-size: 2rem;
  padding: 10px;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: none;
  color: lightgray;
}

header {
  background-image: url("./assets/cover/blue-circular-background.jpg");
  background-size: cover;
  background-attachment: fixed;
  background-position: 80%;
  border-bottom: 3px solid var(--secondary);
}

header a {
  color: var(--light);
  text-shadow: 0 0 8px var(--dark);
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);
}

button:hover {
  opacity: 0.8;
}

button[disabled] {
  cursor: default;
  opacity: 0.8;
}

img {
  max-width: 100%;
}

/* span[role="img"] {
  /* cursor: default; */
/* } */

form {
  width: 50%;
}

.form-control {
  width: 100%;
  max-width: 100%;
  padding: 10px;
}
.label {
  line-height: 200%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

li {
  list-style: none;
}

nav li:hover {
  text-decoration: none;
  color: lightgray;
}

nav li {
  cursor: pointer;
  font-family: "Courier New", Courier, monospace;
  text-shadow: 0 0 8px var(--dark);
  color: var(--light);
}

textarea {
  width: 600px;
  height: 120px;
  border: 3px solid #cccccc;
  padding: 5px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.img-thumbnail {
  width: auto;
  height: 300px;

  border-radius: 5% 5% 20% 5%;
  box-shadow: 0 0 10px;
}

.modalBackdrop {
  background: rgba(0, 0, 0, 0.65);
  bottom: 0;
  left: 0;
  overflow: auto;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1;
}

.modalContainer {
  background: var(--darker);
  border-radius: 5px;
  max-height: 95vh;
  margin: 20px auto;
  padding: 15px;
  width: 800px;
}

.modalTitle {
  color: var(--light);
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 25px;
}
.modalImg {
  max-height: 70vh;
}

button {
  background: var(--primary);
  border: 0;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 30px;
  padding: 10px 15px;
}

a {
  border-radius: 5px;
  color: var(--secondary);
  font-weight: 600;
  padding: 2px 4px;
  margin-left: -2px;
  margin-right: -2px;
}

.navActive {
  color: var(--secondary);
}

.navActive:hover {
  color: var(--secondary);
}

#profileImg {
  max-width: 200px;
  border-radius: 20% 30% 60% 30%;
  box-shadow: 0 0 10px;
}

.footer {
  justify-content: center;
}

footer i {
  font-size: 3rem;
}

#copy {
  text-align: center;
}

#resume {
  text-align: center;
}

#resume ul li {
  margin: 0;
  padding: 0;
}

h6 i {
  font-size: 1rem;
}
article {
  margin: 1rem 2rem 1rem 0;
  position: relative;
  align-items: center;
  vertical-align: baseline;
  /* cursor: pointer; */
}

.caption {
  display: inline-block;
  position: absolute;
  padding: 1rem;
  border-top: var(--dark) 1px solid;
  border-right: var(--dark) 1px solid;
  border-bottom: var(--dark) 1px solid;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  bottom: 50px;
  left: 0;
  color: var(--light);
  background-color: var(--primary);
}

@media screen and (max-width: 980px) {
  form {
    width: 75%;
  }


}

@media screen and (max-width: 768px) {
  form {
    width: 95%;
  }
}

@media screen and (max-width: 575px) {
  form {
    width: 100%;
  }
  h2 {
    font-size: 1.5rem;
    padding: 5px;
    max-width: 100%;
    margin: 0 auto;
  }
}